      for (const member of members) {
        const student = await ctx.db.classMate({ id: member.id }).student()
        pubsub.publish(CLASSGROUP_CHANGED, { [CLASSGROUP_CHANGED]: { "text": student.id } })
      }
      return myClassGroups[0]
